:root {
    --bg:  #242526;
    --bg-accent: #484a4d;
    --text-color: #dadce1;
    --nav-size: 60px;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
    --speed: 500ms; 
}

a {
    color: #dadce1;
    color: var(--text-color);
    text-decoration: none;
}

.admin-nav-bar{
    height: 60px;
    height: var(--nav-size);
    background-color: #242526;
    background-color: var(--bg);
    padding: 2px;
    border-bottom: 1px solid #474a4d;
    border-bottom: var(--border);
}

.admin-nav-bar-nav{
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.admin-nav-item{
    width: calc(60px * 0.8);
    width: calc(var(--nav-size) * 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 25px;
}


.icon-button{
    --button-size: calc(var(--nav-size) * 0.5);
    width: var(--button-size);
    height: var(--button-size);
    background-color: #484a4d;
    border-radius: 50%;
    padding: 5px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.icon-button:hover{
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}

.icon-button svg{
    fill: #dadce1;
    fill: var(--text-color);
}

.dropdown{
    position: absolute;
    top: 58px;
    width: 300px;
    transform: translate(-45%);
    background-color: #242526;
    background-color: var(--bg);
    border: 1px solid #474a4d;
    border: var(--border);
    border-radius: 8px;
    border-radius: var(--border-radius);
    padding: 1rem;
    overflow: hidden;
}

.menu-item{
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    border-radius: var(--border-radius);
    transition: background 500ms;
    transition: background var(--speed);
    padding: 0.5rem;
}

.menu-item:hover{
    background-color: #525357;
}


