.base-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    

}


.base-container .header{
    padding-top: 20px;
    font-size: 24px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #2a327d;

}

.base-container .content{
    display: flex;
    flex-direction: column;

}

.base-container .image{
    width: 21em;
}

.base-container .image img{
    width: 100%;
    height: 100%;
    
}


.base-container .form{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;

}


.base-container .form .forn-group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: fit-content;
}

.base-container .form .form-group label{
    font-size: 20px;

}

.base-container .form .form-group input{
    margin-top: 6px;
    min-width: 18em;
    height: 37px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
    
}

.base-container .form .form-group input:focus{
    outline: none;
    box-shadow: 0px, 0px, 12px, 0.8px #0e81ce96;

}

.footer{
    padding-bottom: 10px;
}